}

:root {
	--primary: #fff;
}

.btn {
	margin-top: 0px;
	padding: 8px 20px;
	border-radius: 2px;
	outline: none;
	border: none;
	cursor: pointer;
}

.btn--primary {
	background-color: #fff;
	background-color: var(--primary);
	color: #242424;
	border: 1px solid #fff;
	border: 1px solid var(--primary);
}

.btn--outline {
	background-color: transparent !important;
	color: #fff !important;
	padding: 8px 20px !important;
	border: 1px solid #fff !important;
	border: 1px solid var(--primary) !important;
	transition: all 0.3s ease-out !important;
}

.btn--medium {
	padding: 8px 20px;
	font-size: 18px;
}

.btn--large {
	padding: 12px 26px !important;
	font-size: 20px !important;
}

.btn--large:hover,
.btn--medium:hover {
	transition: all 0.3s ease-out !important;
	background: #fff !important;
	color: #242424 !important;
	transition: 250ms !important;
}

.navbar-nav {
	background: linear-gradient(
		90deg,
		rgb(28, 27, 27) 0%,
		rgb(26, 23, 23) 100%
	);
	height: 80px;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.2rem;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 999;
}

.navbar-container {
	display: flex;
	justify-content: center;
	align-items: center;
	height: 80px;
}

.navbar-logo {
	color: #fff;
	justify-self: start;
	cursor: pointer;
	text-decoration: none;
	font-size: 2rem;
	display: flex;
	align-items: center;
}

.fa-shekel-sign {
	margin-left: 0.5rem;
	margin-top: 3px;
	font-size: 1.8rem;
}

.nav-menu {
	display: grid;
	grid-template-columns: repeat(3, auto);
	list-style: none;
	text-align: center;
	width: 60vw;
	justify-content: end;
	margin-right: 1rem;
	margin-bottom: 0;
}

.nav-item {
	height: 80px;
}

.nav-links {
	color: #fff;
	display: flex;
	align-items: center;
	text-decoration: none;
	padding: 0.5rem 1rem;
	height: 100%;
}

.nav-links:hover {
	border-bottom: 4px solid #fff;
	transition: all 0.2s ease-out;
}

.fa-bars {
	color: #fff;
}

.nav-links-mobile {
	display: none !important;
}

.menu-icon {
	display: none;
	margin-right: 10px;
}

@media screen and (max-width: 960px) {
	.NavbarItems {
		position: relative;
	}

	.nav-item {
		height: 80px;
	}

	.nav-item.button {
		height: 120px;
	}

	.nav-menu {
		display: flex;
		flex-direction: column;
		width: 100%;
		position: absolute;
		top: 80px;
		left: -100%;
		opacity: 1;
		transition: all 0.5s ease;
	}

	.nav-menu.active {
		background: #242222;
		left: 0;
		opacity: 1;
		transition: all 0.5s ease;
		z-index: 1;
	}

	.nav-links {
		text-align: center;
		padding: 2rem;
		width: 100%;
		display: table;
	}

	.nav-links:hover {
		background-color: #fff;
		color: #242424;
		border-radius: 0;
	}

	.navbar-logo {
		position: absolute;
		top: 0;
		left: 0;
		-webkit-transform: translate(25%, 50%);
		        transform: translate(25%, 50%);
	}

	.menu-icon {
		display: block;
		position: absolute;
		top: 0;
		right: 0;
		-webkit-transform: translate(-100%, 60%);
		        transform: translate(-100%, 60%);
		font-size: 1.8rem;
		cursor: pointer;
	}

	.fa-times {
		color: #fff;
		font-size: 2rem;
	}

	.nav-links-mobile {
		display: block !important;
		text-align: center !important;
		margin: 2rem auto !important;
		border-radius: 4px !important;
		width: 80% !important;
		text-decoration: none !important;
		font-size: 1.5rem !important;
		background-color: transparent !important;
		color: #fff !important;
		padding: 14px 20px !important;
		border: 1px solid #fff !important;
		transition: all 0.3s ease-out !important;
	}

	.nav-links-mobile:hover {
		background: #fff !important;
		color: #242424 !important;
		transition: 250ms !important;
	}
}

